//GET USER TOKEN BALANCE
export async function userTokenBalance(wallet) {
    try {
        const contract = new web3.eth.Contract(tokenABI, tokenContract);
        let response = await contract.methods.balanceOf(wallet).call();
       // console.log('user USDT balance', response / (10 ** 6))
        return response / (10 ** 18);
    } catch (error) {
        console.log(error)
    }
}

