//SET USER TRANSFER ALLOWANCE
export async function approve(wallet, amount) {
    amount = formatBigNumber(Math.ceil(amount * (10 ** 18))).toString();
    const contract = new web3.eth.Contract(tokenABI, tokenContract);
    let data = await contract.methods.approve(USDMinterContract, amount).encodeABI();
   // console.log("allowance data", data)
    const transationData = { from: wallet, to: tokenContract, gas: '210000', value: '0', data}
    return transationData;
}


//GET APPROVE ALLOWANCE FOR TOKEN TRANSFER
export async function allowance(wallet) {
    try {
        const contract = new web3.eth.Contract(tokenABI, tokenContract);
        let response = await contract.methods.allowance(wallet, USDMinterContract).call();
       // console.log('approved allowance', response / (10 ** 6))
        return parseFloat(response / (10 ** 18));
    } catch (error) {
        console.log(error)
    }
}


