//STAKE TOKENS
export async function invest(wallet, amount) {
    const ref = localStorage.getItem('refa');
    amount = formatBigNumber(Math.ceil(amount * (10 ** 18))).toString();
     //console.log('num 2', amount, wallet, ref);
     const contract = new web3.eth.Contract(contractABI, USDMinterContract);
    let data = await contract.methods.stakeStablecoins(amount, ref).encodeABI();
    //console.log(data)
    const transationData = { from: wallet, to: USDMinterContract, gas: '730000', value: '0', data}
    // console.log("transationData", transationData)
    return transationData;
}


